import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as action from '../actions/actions';
// import ToDoList from "./toDoList";

import { Button, Input  } from 'antd';
//antd
// import 'antd/lib/button/style/index.less';
// import 'antd/lib/input/style/index.less';
// antd
// import "antd/dist/antd.less"

// json
import Person from '../../api/person.json';

import 'styles/main.css';
import 'styles/index.scss';

class App extends Component {
    constructor(props) {
        super(props);
        console.log(Person.name);
    }
    render() {
        var str = 'React-App';
        console.log('${str}');
        return (
            <div>
                <hr/>
                <Input placeholder="" type="text"  ref="input"/>
                <Button type="primary" onClick={(e) => this.handleClick(e) }>add</Button>
                <hr/>
                <img src={require('../images/weixin.jpg')}/>
                <div>
                    {
                        this.props.items.map(function (item) {
                            return <div className="item" key={item.ids}>{item.name}</div>;
                        })
                    }
                </div>
            </div>
        );
    }

    handleClick(e) {
        const node = ReactDOM.findDOMNode(this.refs.input);
        console.log(node);
        const text = node.value.trim();
        if (!text) return;
        let id = Math.random();
        this.props.addItem(id, text);
        node.value = '';
    }
}


function mapStateToProps(state) {
    console.log(state.items);
    return {
        items: state.items
    };
}


// function buildActionDispatcher(dispatch) {
//     return {
//         action: bindActionCreators(action, dispatch)
//     }
// }

//statepropsactionprops
export default connect(mapStateToProps, action)(App);






